.boardWrapper {
  background-color:rgb(218, 199, 167);
  background:url('/wood.jpg');
  -webkit-box-shadow: -3px -3px 30px rgba(0,0,0,0.5);
          box-shadow: -3px -3px 30px rgba(0,0,0,0.5);
  position: relative;
}

.board {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  position: relative;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.placeBox {
  border-right:1px solid black;
  border-bottom:1px solid black;
  -ms-flex-positive:1;
      flex-grow:1;
}
.placeBox:first-child {
  border-left:1px solid black;
}
.row:first-child .placeBox {
  border-top:1px solid black;
}
.row .placeBox:last-child {
  border:0px;
}
.row:last-child .placeBox {
  border:0px;
}

.place {
  height:100%;
  width:100%;
  background-size: 97% 97%;
  background-position: center;
  background-repeat: no-repeat;
  position:relative;
  bottom:50%;
  right:50%;
}
.place:hover {
  -webkit-box-shadow:0px 0px 5px white;
          box-shadow:0px 0px 5px white;
}

div.black {
  background-image: url("/stone_black.svg")
}

div.white {
  background-image: url("/stone_white.svg")
}


