body {
  font-family: 'Indie Flower', cursive;
  height:100vh;
  width:100vw;
  margin: 0;
  padding: 0;
  background: -webkit-gradient(linear, left top, right top, from(rgb(9, 54, 9)), to(rgb(55, 109, 36)));
  background: -webkit-linear-gradient(left, rgb(9, 54, 9), rgb(55, 109, 36));
  background: -o-linear-gradient(left, rgb(9, 54, 9), rgb(55, 109, 36));
  background: linear-gradient(left, rgb(9, 54, 9), rgb(55, 109, 36));
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color:rgb(194, 194, 194);
  text-shadow: 1px 1px 3px black;
  font-size: 100%;
}
#root {
  width:100vw;
  height:100vh;
}

button {
  cursor:pointer;
}


