.MainMenu {
   height:100vh;
   width:100vw;
   display:-ms-flexbox;
   display:flex;
   -ms-flex-direction: column;
       flex-direction: column;
   -ms-flex-pack: distribute;
       justify-content: space-around;
   -ms-flex-align: center;
       align-items: center;
   font-size:4vh;
}

.MainMenu h1 {
   margin:0px;
}
.MainMenu ul {
   text-align: center;
   margin:0px;
   padding:0px;
}
.MainMenu ul li {
   list-style-type: none;
   margin:0px;
   padding:0px;
   cursor:pointer;
}
.MainMenu button {
   margin:0px;
}

.MainMenu ul li.selected::before {
   content: '-> ';
}
.MainMenu ul li.selected::after {
   content: ' <-';
}



